import 'tui-image-editor/dist/tui-image-editor.css'
import ImageEditor from '@toast-ui/react-image-editor'
import { Component } from 'react'

export default class App extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      picture: null,
      axelorAppName: 'pizza-planet',
      param: 'photoId',
      domain: 'com.axelor.photoalbum.db.Photo'
    }

    this.getPicture = this.getPicture.bind(this)
  }

  getPicture() {
    const params = new URLSearchParams(document.location.search)
    const id = 1
    console.log(params)
    console.log("id: " + id)
    if(id){
      fetch(`/${this.state.axelorAppName}/ws/rest/${this.state.domain}/${id}/`, {method: 'GET'})
      .then(response => response.json())
      .then(json => {
          this.setState({
            picture: `/${this.state.axelorAppName}/ws/rest/com.axelor.meta.db.MetaFile/${json.data[0].picture.id}/content/download`
          }, () => console.log(this.state.picture))
      })
      .catch(err => console.error(err))
    }
  }

  componentDidMount() { this.getPicture() }
  
  render() {
    return (
      this.state.picture && <div className="App">
        <ImageEditor
          includeUI={{
            loadImage: {
              path: this.state.picture,
              name: 'Your picture',
            },
            menu: ['shape', 'filter'],
            initMenu: 'filter',
            uiSize: {
              width: '640px',
              height: '640px',
            },
            menuBarPosition: 'bottom',
          }}
          cssMaxHeight={640}
          cssMaxWidth={640}
          selectionStyle={{
            cornerSize: 20,
            rotatingPointOffset: 70,
          }}
          usageStatistics={false}
        />
      </div>
    )
  }
}
